@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400&display=swap);
body {
	font-family: 'Poppins', sans-serif;
}
.main-container {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}

button {
	font-family: 'Poppins', sans-serif;
	border-radius: 0.25rem;
}

.error {
	background-color: rgb(255, 220, 220);
	border: solid 0.5px rgb(255, 132, 132);
	border-radius: 0.25rem;
	padding: 0.3rem;
	font-size: small;
	width: 50vh;
	text-align: center;

}

.question {
	height: 20vh;
	text-overflow: ellipsis;
	text-align: center;
	overflow-wrap: break-word;
}

.answers-container {
	display: flex;
	flex-direction: column;
	width: 60vh;
}

.answer {
	height: 3.5rem;
	background-color: #ffffff;
	border: solid 1px #482c78;
	border-radius: 0.25rem;
	margin: 1.5rem;
	font-size: smaller;
	font-family: 'Roboto', sans-serif;
}

.selected {
	background-color: #ece7f2;
	color: #482c78;
	font-weight: bold;
}

.results-container {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	height: 100vh;
}

.result {
	font-family: 'Roboto', sans-serif;
	color: #88a957;
}

.result h2.dosha {
	color: aqua;
}

.next-question {
	background-color: #7857A9;
	color: white;
	border: none;
	box-shadow: 0;
}

.restart {
	background-color: #5760a9;
	color: white;
	border: none;
	box-shadow: 0;
	height: 3rem;
	width: 9rem;
}
